import { Component , ViewChild} from '@angular/core';

/**
 * Generated class for the BarcodeInputComponent component.
 *
 * See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
 * for more info on Angular Components.
 */
@Component({
  selector: 'barcode-input',
  templateUrl: 'barcode-input.html'
})
export class BarcodeInputComponent {

//  @ViewChild('innerInputBox') innerInputBox ;

  value: string = '';
  _isFocused :boolean = false; 

  constructor() {
    this.value = '';
  }

  onClick()
  {
  	this._isFocused = true;
  }

  onKeyUp(event:KeyboardEvent)
  {
  	this.value += event.which;
  	//this.value += event.key;
  	console.log(event);
  }

}



