import { SharedModule } from './shared.module';
import { NgModule, ErrorHandler } from '@angular/core';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';

import { MyApp } from './app.component';
import { MODULES, PROVIDERS } from './app.imports';

import { Http } from '@angular/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
export function HttpLoaderFactory(http: Http) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    MODULES,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/about/about.module#AboutPageModule', name: 'AboutPage', segment: 'about', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/confirm-qty/confirm-qty.module#SignupModalPageModule', name: 'ConfirmQtyPage', segment: 'confirm-qty', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home-screen/home-screen.module#HomeScreenPageModule', name: 'HomeScreenPage', segment: 'home-screen', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/order-status/order-status.module#OrderStatusPageModule', name: 'OrderStatusPage', segment: 'order-status', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/place-in-tote/place-in-tote.module#PlacInTotePageModule', name: 'PlaceInTotePage', segment: 'place-in-tote', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/popup-modal/alert-modal/alert-modal.module#HintModalPageModule', name: 'AlertModalPage', segment: 'alert-modal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/popup-modal/confirm-modal/confirm-modal.module#SignupModalPageModule', name: 'ConfirmModalPage', segment: 'confirm-modal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/popup-modal/prompt-modal/prompt-modal.module#WalkthroughModalPageModule', name: 'PromptModalPage', segment: 'prompt-modal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/scan-order/scan-order.module#ScanOrderPageModule', name: 'ScanOrderPage', segment: 'scan-order', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/scan-product/scan-product.module#ScanProductPageModule', name: 'ScanProductPage', segment: 'scan-product', priority: 'low', defaultHistory: [] }
  ]
}),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [Http]
      }
    }),
    SharedModule,
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    PROVIDERS, 
    // Keep this to enable Ionic's runtime error handling during development
    { provide: ErrorHandler, useClass: IonicErrorHandler },
  ]
})
export class AppModule { }



